.search-form {


    &__input {
        background-color: transparent;
        border: 1px solid $color-primary--darkest;
        border-radius: 1px;
        padding: 0.8rem 5.5rem;
        font-weight: 400;
        font-family: $font-text;
        font-size: 1.1rem;
        color: $color-gray--dark;
        width: 70%;
    }

    &__input::-webkit-input-placeholder {
        font-family: $font-text;
        font-weight: 700;
        font-size: 0.8rem;
    }


}